@font-face {
    font-family: fontmedium;
    src: url(../fonts/Montserrat-Medium.ttf);
}

@font-face {
    font-family: fontsemibold;
    src: url(../fonts/Montserrat-SemiBold.ttf);
}

.fontMedium {
    font-family: fontmedium;
    font-size: 15px !important;
    color: #263F56 !important;
}

.fontInput {
    font-family: fontmedium;
    font-size: 13px;
   
}


.fontTitle {
    font-family: fontsemibold;
    font-size: 20px;
    color: #263F56 !important;
}

.fontSemiBold {
    font-family: fontsemibold;
    color: #263F56 !important;
}

h4,
h1,
h2,
h3,
h5,
h6 {
    color: #263F56;
    font-family: fontsemibold;
}

p,
a {
    color: #263F56;
    font-family: fontmedium;
}

.text-green {
    color: #54D19E !important;
}

.centrado {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 50vh;
}

.align-text{
    text-align: left;
}


@media only screen and (max-width: 790px) {
    .hideCalendar {
      display: none !important;
    }
  
  }